<template>
<div class="login">
  <div class="logo">
        <a href="http://127.0.0.1:8083/#/index"
          ><img :style="{ height: '80px' }" src="../imgs/img2.png" alt=""
        /></a>
  </div>
  <div class="content">
    <div class="login-wrapper">
      <div class="title">账户注册</div>
      <div class="item">
        <span>账户名：</span>
    <el-input
                v-model="userName"
                placeholder="请输入你的用户名"
              ></el-input>
      </div>
      <div class="item">
        <span>密码:</span>
          <el-input
                show-password
                v-model="password"
                placeholder="请输入密码"
              ></el-input>
      </div>
                  <el-button class="login-btn" @click="login" type="primary"
              >立即注册</el-button
            >
      <div class="register-info" @click="locationFn('/login')">已有账号，去登录></div>
    </div>
  </div>
</div>
</template>

<script>
/* eslint-disable */

	import http from './http';
import api from './_api';

	export default {
	    data(){
			return {

        userName:'',
				password: '',
        code:null,
        email: '',
        time:60,
        start: false,
        show: true,

			}
		},
		mounted(){
		},
		computed: {
		},
		methods: {
			locationFn(url){
					this.$router.push(url)
			},

			login(){
        if(!this.userName){
						this.$message({
								type: 'error',
								message: '请输入用户名'
						});
            return;
        }
        if(!this.password){
						this.$message({
								type: 'error',
								message: '请输入密码'
						});
            return;
        }
        http.post(api.register,{userName:this.userName,password:this.password},1).then((res)=>{
            if(res.status === 10000){
                this.$message({
                  type: 'success',
                  message: '注册成功'
								});
                this.$router.push('/login')
            }else{
								this.$message({
										type: 'error',
										message: res.msg
								});
            }
        })
			},
		}
	}
</script>

