<template>
  <div>
    <div>
      <HeaderCom></HeaderCom>
      <div class="header">
        <span  class="desc">我的订单</span>
      </div>
      <div class="content">

<el-card
v-for="(item, index) in orders"
  :key="index"
 class="box-card">
  <template #header>
    <div class="card-header">
      <div class="desc-wrapper">
        <span>订单号：{{item.orderNo}}</span>
        <span>下单时间：{{getTime(item.createTime)}} </span>
        <span>订单状态：<i :style="{color:'#e4393c'}">{{item.orderStatusName}}</i> </span>
      </div>
      <el-button class="button" type="text">
        <a
                                      @click="
                                        locationFn(
                                          '/order_detail?orderId=' +
                                            item.orderNo
                                        )
                                      "
                                      >订单详情</a
                                    ></el-button>
    </div>
  </template>
    <div>
                      <div
v-for="(_item, _index) in item.orderItemVOList"
                        :key="_index"
                        :style="{ height: 'auto',marginTop:'10px', display:'flex',justifyContent:'center' }"
                      >
                                        <div class="good-item">
                                            <a>
                                              <img
                                                :src="_item.productImg"
                                                width="80"
                                                height="80"
                                                alt=""
                                              />
                                            </a>
                                          </div>
                                          <p class="good-item">
                                            <a
                                              @click="locationFn('/detail?id=' + _item.productId)"
                                              >{{ _item.productName }}
                                              </a>
                                          </p>
                                          <p class="good-item"
                                          >
                                            {{
                                              (_item.unitPrice / 100).toFixed(
                                                2
                                              )
                                            }}元 × {{ _item.quantity }}
                                          </p>
                      </div>
  </div>

</el-card>
      </div>

        <el-pagination
          :style="{ textAlign: 'right', marginBottom:'60px' }"
          v-if="total > 0"
          layout="prev, pager, next"
          :page-size="3"
          :total="total"
          :current-page="current"
          @current-change="pageChange"
        >
        </el-pagination>

        <div v-if="!orders || orders.length === 0" class="empty">
  暂无订单数据，请下单
</div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
/* eslint-disable */

import HeaderCom from '../components/header-com.vue';
import Footer from '../components/footer-com.vue';
import moment from 'moment-mini'

    import http from './http'
    import api from './api'
    export default {
        data(){
            return {
                orders:[
                    
                ],
                current: 1,
                total:0,
            }
        },
    	components: {
    		HeaderCom,
            Footer,
    	},
        created(){
        },
        mounted(){
                this.getData();
        },
        methods: {
                        	locationFn(url){
					this.$router.push(url)
			},
            pageChange(cur){
                this.current =cur;
                this.getData();
            },
            getTime(t){
              return moment(t).format('YYYY.MM.DD HH:mm:ss')
            },
            getData(){
                http.get(api.orderList,{
                    pageSize: 3,
                    pageNum: this.current,
                }).then((res)=>{
                    if(res.status === 10000){
                        this.orders = res.data.list;
                        this.total = res.data.total;
                    }else{
                        this.$message({
                            type: 'error',
                            message: res.msg
                        }); 
                    }
                })
            }
        },
    }
</script>

<style lang="less" scoped>
  .header{
    display: flex;
    align-items: center;
    padding: 10px;
    .desc{
      font-size: 24px;
    }
    .el-progress{
      width: 300px;
      margin-left: 100px;
    }
  }
  .good-item{
    flex:1
  }
  .card-header{
    text-align: left;
    display: flex;
    justify-content: space-between;
  }
  .box-card{
    margin-top: 20px;
    width: 800px!important;
  }
  .empty{
    font-size: 20px;
  }
  .desc-wrapper{
    display: flex;
    flex-direction: column;
  }
</style>
