<script>
/* eslint-disable */

import HeaderCom from '../components/header-com.vue';
import Footer from '../components/footer-com.vue';
import VueSlickCarousel from 'vue-slick-carousel';
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
import http from './http';
import api from './api';

console.log('pppp')
    export default {
			components:{
				HeaderCom,
				Footer,
				VueSlickCarousel,
			},
			data(){
    		return {
					types:[],
					productList:[],
					settings:{
						dots: true,
						infinite: true,
						centerMode: true,
						slidesToScroll: 1,
						centerPadding: '150px',
						variableWidth: true,
						initialSlide: 1,
						autoplay: true,
					}
    		}
    	},
			mounted(){
					this.getData();
					this.getProduct();
          console.log('carousel__container:::',document.getElementsByClassName('el-carousel__container'))
          document.getElementsByClassName('el-carousel__container')[0].style.height = '100%'
			},
			methods: {

        locationFn(url){
            this.$router.push(url)
        },
        getProduct(){
          http.get(api.productList,{pageSize: 20},1).then((res)=>{
            this.productList = res.data.list;
          })
        },

        getData(){
            http.get(api.categoryList,null,1).then((res)=>{
              this.types = res.data
              console.log('types::',this.types);
            })
        }
			},
    }
</script>


