<template>
  <div :style="{ backgroundColor: '#ffffff' }">
    <HeaderCom></HeaderCom>
      <div className="search">
                {{ deUrl() ? "搜索‘" + decodeURI(deUrl()) + "’" : "" }}
      </div>
      <div class="btn-wrapper">
        <el-button  size="small" @click="getData2" type="primary">综合排序</el-button>
        <el-button  size="small" @click="getDescData" type="primary">价格</el-button>
      </div>

      <div class="goods">
        <div
        class="good-item"
            v-for="(item, index) in goods"
            :key="index"
            @click="locationFn('/detail?id=' + item.id)"
        >
          <img :src="item.image" alt="" />
          <p class="name">{{ item.name }}</p>
          <p class="price">{{ item.price / 100 }}元</p>
        </div>
      </div>

            <div
        :style="{
          display: 'flex',
          justifyContent: 'center',
          marginBottom: '60px',
        }"
      >
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="current"
          layout=" prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
    <Footer></Footer>
  </div>
</template>

<script>
/* eslint-disable */

import HeaderCom from '../components/header-com.vue';
import Footer from '../components/footer-com.vue';
import http from './http'
import api from './api'

	export default {
	    data(){
			return {
        desc: false,
        current: 1,
        total: 0,
        goods:[]
			}
		},
    components: {
      HeaderCom,
          Footer,
    },
		mounted(){
			this.current =1;
			this.getData();
		},
		watch: {
      $route: {
            handler() {
              console.log('=====')
              this.getData();
                //深度监听，同时也可监听到param参数变化
          },
          // deep: true,
      }
  },
		computed: {
		},
		methods: {
			locationFn(url){
					this.$router.push(url)
			},
			deUrl(){
				return this.getUrlkey(window.location.href).keyword
			},
			handleCurrentChange(cur){
				this.current = cur;
				this.getData();
			},
			getUrlkey(url) {
					var params = {};
					var urls = url.split("?");
					if(urls[1]){
							var arr = urls[1].split("&");   
							for (var i = 0, l = arr.length; i < l; i++) {
									var a = arr[i].split("=");               
									params[a[0]] = a[1];                     
								}               
					}                                                    
					return params;
			},
      getDescData(){
        this.desc = !this.desc;
        this.getData()
      },
			getData(){
							const hash = window.location.hash.split('=')[1] ? window.location.hash.split('=')[1] : '';
							const hash2 = window.location.hash.split('==')[1] ? window.location.hash.split('==')[1] : '';
              let that = this;
							http.get(api.productList,{
									keyword:hash2 ? '' : decodeURI(hash),
									orderBy: that.desc ? 'price desc' : 'price asc',
									categoryId: decodeURI(hash2),
									pageSize: 10,
									pageNum: this.current,
							},1).then((res)=>{
								this.goods = res.data.list;
								this.total = res.data.total;
							})
			},
			getData2(){
        const hash = window.location.hash.split('=')[1] ? window.location.hash.split('=')[1] : '';
        const hash2 = window.location.hash.split('==')[1] ? window.location.hash.split('==')[1] : '';
        http.get(api.productList,{
            keyword:hash2 ? '' : decodeURI(hash),
            // orderBy: this.state.desc ? 'price desc' : 'price asc',
            categoryId: decodeURI(hash2),
            pageSize: 10,
            pageNum: this.current,
        },1).then((res)=>{
            if(res.status === 10000){
							this.goods = res.data.list;
								this.total = res.data.total;
            }
        })
    }
		}
	}
</script>

<style lang="less" scoped>

.search{
  text-align: left;
}
.btn-wrapper{
  text-align: left;
}

  .goods{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 60px;
    justify-content: space-around;
    .good-item{
      flex: 0 0 20%;
      margin: 10px;
      img{
        width: 150px;
        height: 150px;
      }
    }
  }

</style>
