<template>
  <div>
    <HeaderCom></HeaderCom>
    <div class="content">
      <div class="header">
        <div class="left">
        <span  class="desc">订单详情</span>
        <el-progress v-if="status === 0" :text-inside="true" :stroke-width="20" :percentage="30" status="exception">
           <span>已取消</span>
        </el-progress>
        <el-progress v-if="status === 10" :text-inside="true" :stroke-width="20" :percentage="35" status="exception">
           <span>待付款</span>
        </el-progress>
        <el-progress v-if="status === 20" :text-inside="true" :stroke-width="20" :percentage="70" status="warning">
           <span>付款成功</span>
        </el-progress>
                <el-progress v-if="status === 30"  :text-inside="true" :stroke-width="20" :percentage="90" status="warning">
           <span>出库成功</span>
        </el-progress>
                <el-progress v-if="status === 40"  :text-inside="true" :stroke-width="20" :percentage="100" status="success">
           <span>交易成功</span>
        </el-progress>
        </div>
        <el-button  size="small" @click="locationFn('/orders')" type="primary">返回订单列表</el-button>
      </div>
      <div class="info-wrapper">
              <div class="info">
                <el-card class="box-card">
  <template #header>
    <div class="card-header">
      <span>收件人信息</span>
    </div>
  </template>
          <div
            className="items"
            :style="{ display: 'flex', alignItems: 'center', width: '100%' }"
          >
            <span :style="{  fontSize: '16px' }">收件人：{{name}}</span>
          </div>
          <div
            className="items"
            :style="{
              display: 'flex',
              alignItems: 'center',
              width: '100%',
              marginTop: '20px',
            }"
          >
            <span :style="{ fontSize: '16px' }">电话：{{phone}}</span>
          </div>
          <div
            className="items"
            :style="{
              display: 'flex',
              alignItems: 'center',
              width: '100%',
              marginTop: '20px',
            }"
          >
            <span :style="{ fontSize: '16px' }">地址：{{address}}</span>
          </div>
</el-card>

                <el-card class="box-card" :style="{marginTop:'20px'
                        }">
  <template #header>
    <div class="card-header">
      <span>商品信息</span>
    </div>
  </template>
  <div>
                      <div
                        :style="{
                          display: 'flex',
                          alignItems: 'center',
                          marginTop: '10px',
                          justifyContent: 'space-around',
                        }"
                        v-for="(item, index) in goods"
                        :key="index"
                      >
                        <img
                          :src="item.productImg"
                          width="80"
                          height="80"
                          alt=""
                        />
                        <div
                          target="_blank"
                          :style="{width: '300px'}"
                          @click="locationFn('/detail?id=' + item.productId)"
                          >{{ item.productName }}
                        </div>
                        <p
                          class="price"
                        >
                          {{ item.unitPrice / 100 }}元 × {{ item.quantity }}
                        </p>
                      </div>
  </div>

</el-card>
      </div>
      </div>
    </div>
    <div class="footer">
      <p>商品总价 <i>{{ totalPrice / 100 }}</i>元</p>
                   <el-button  v-if="status === 10" class="login-btn" @click="cancelOrder" type="danger"
              >取消订单</el-button
            >
                   <el-button v-if="status === 10" class="login-btn" @click="toPay" type="primary"
              >去支付</el-button
            >
       <el-button v-if="status === 0" class="login-btn" type="success"
              >已取消</el-button
            >
            <el-button v-else-if="status !== 40" class="login-btn" @click="getGood" type="success"
              >确认收货</el-button>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
/* eslint-disable */

import HeaderCom from '../components/header-com.vue';
import NavCom from '../components/nav-com.vue';
import Footer from '../components/footer-com.vue';
import http from './http'
import api from './api'
    export default {
        data(){
            return {
                id:'',
                current: 0,
                address:'',
                address_money: 10,
                all:20,
                name:'',
                phone: '',
                status: '',
                totalPrice:'',
                goods:[
                ]
            }
        },
    	components: {
    		HeaderCom,
            NavCom,
            Footer,
    	},
        created(){
        },
        mounted(){

                this.getData();
        },
        methods: {
            toPay(){
                 this.$router.push('/pay?orderNo='+window.location.hash.split('=')[1])
            },
            getGood(){
                http.post(api.orderFinish,{orderNo: window.location.hash.split('=')[1]}).then((res)=>{
                                                    if(res.status === 10000){
                                                        this.$message({
                                                            type: 'success',
                                                            message: '收货成功'
                                                        }); 
                                                        this.$router.push('/orders');
                                                    }else{
                                                         this.$message({
                                                            type: 'error',
                                                            message: res.msg
                                                        }); 
                                                    }
                                                })
            },
            	locationFn(url){
					this.$router.push(url)
			},
            cancelOrder(){
      http.post(api.orderCancel,{orderNo: window.location.hash.split('=')[1]}).then((res)=>{
                                                                        if(res.status === 10000){
                                                                             this.$message({
                    type:'success',
                    message:'取消成功'
                })
                        this.$router.push('/orders')
                                                                        }else{
                                                                              this.$message({
                    type:'error',
                    message:res.msg
                })
                                                                        }
                                                                    })
            },
    getData(){
        http.get(api.orderDetail,{orderNo:window.location.hash.split('=')[1]}).then((res)=>{
            if(res.status === 10000){
                this.goods = res.data.orderItemVOList;
                this.name = res.data.receiverName;
                this.phone = res.data.receiverMobile;
                this.address = res.data.receiverAddress;
                this.totalPrice = res.data.totalPrice;
                this.status = res.data.orderStatus;
            }else{
                this.$message({
                    type:'error',
                    message:res.msg
                })
            }
        })
    }
        },
    }
</script>
