<template>

<div class="login">
  <div class="logo">
        <a href="http://127.0.0.1:8083/#/index"
          ><img :style="{ height: '80px' }" src="../imgs/img2.png" alt=""
        /></a>
  </div>
  <div class="content">
    <div class="login-wrapper">
      <div class="title">账户登录</div>
      <div class="tip">用户名imooc 密码12345678</div>
      <div class="item">
        <span>账户名：</span>
        <el-input
          v-model="loginForm.userName"
          placeholder="请输入你的用户名"
        ></el-input>
      </div>
      <div class="item">
        <span>密码:</span>
              <el-input
                type="password"
                show-password
                v-model="loginForm.password"
                placeholder="请输入你的用户名"
              ></el-input>
      </div>
                  <el-button class="login-btn" @click="login" type="primary"
              >立即登录</el-button
            >
      <div class="register-info" @click="locationFn('/register')">立即注册></div>
    </div>
  </div>
</div>
</template>

<script>
/* eslint-disable */
	import http from './http';
import api from './api';
import { useRouter } from 'vue-router';

const router = useRouter();

	export default {
	    data(){
			return {
				loginForm: {
					username: '',
					password: '',
				}
			}
		},
		mounted(){
      console.log('this::',this);
		},
		computed: {
		},
		methods: {
			locationFn(url){
					this.$router.push(url)
			},
			login(){
        if(!this.loginForm.userName){
						this.$message({
								type: 'error',
								message: '请输入用户名'
						});
            return;
        }
        if(!this.loginForm.password){
						this.$message({
								type: 'error',
								message: '请输入密码'
						});
            return;
        }
        http.get(api.login,{userName:this.loginForm.userName,password:this.loginForm.password},1).then((res)=>{
            if(res.status === 10000){
							      localStorage.setItem('username',res.data.username);
                    localStorage.setItem('personalizedSignature',res.data.personalizedSignature);
                    localStorage.setItem('role',res.data.role);
										this.$router.push('/index')
            }else{
							this.$message({
									type: 'error',
									message: res.msg
							});
            }
        })
			},
		}
	}
</script>

