<template>
  <div :style="{ backgroundColor: '#ffffff' }">
    <HeaderCom></HeaderCom>
      <div class="detail-wrapper">
    <div class="good-detail">
      <div class="title">
        {{ name }}
      </div>
      <div class="bottom">
          <img
            :style="{ width: '500px', height: '500px' }"
            :src="img"
          />
          <div class="desc">
            <div class="subname">
            {{ subName }}
            </div>
          <div class="price">售价：{{ money / 100 }}元</div>
            <el-button class="buy-btn" type="primary" @click="addFn"
              >加入购物车</el-button
            >
          </div>
      </div>
    </div>
      </div>
    <FooterCom></FooterCom>
    <el-dialog title="" v-model="visible" width="30%">
      <div
        :style="{ fontSize: '18px', textAlign: 'center', marginTop: '-20px' }"
      >
        商品已加入购物车，请选择继续购物或结算
      </div>
      <div :style="{ display: 'flex', justifyContent: 'center' }">
        <el-button :style="{ marginTop: '20px' }" @click="visible = false"
          >继续购物</el-button
        >
        <el-button
          :style="{ marginTop: '20px' }"
          type="primary"
          @click="handleOk"
          >去结算</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
/* eslint-disable */

import HeaderCom from '../components/header-com.vue';
import FooterCom from '../components/footer-com.vue';
import http from './http';
import api from './api';

    export default {
        data(){
            return {
                visible: false,
                name: '默认商品',
                subName: '默认描述',
                money:'100.00',
                img: ''
            }
        },
    	components: {
    		HeaderCom,
        FooterCom,
    	},
        created(){
            this.initData();
        },
        methods: {
            addFn(){
                http.post(api.cartAdd,{productId: window.location.hash.split('=')[1],count: 1}).then((res)=>{
                    if(res.status === 10000){
                        this.visible = true;
                    }else{
                        		this.$message({
								type: 'error',
								message: res.msg
						});
                    }
                })
            },
initData(){
       let hash = window.location.hash.split('=')[1]
        http.get(api.productDetail,{
            id:hash
        },1).then((res)=>{
            if(res.status === 10000){
                this.name = res.data.name;
                this.img = res.data.image;
                this.subName = res.data.detail;
                this.money = res.data.price;
                this.stock = res.data.stock;
            }
        })
},
            handleOk(){
                this.$router.push('/cart')
                this.visible = false;
            },

        },
    }
</script>

